<template>
  <div style="display: flex;flex-direction: column;">
    <div style="height:60px;display: flex;align-items: center;">
      <span>DPI级数</span>
      <select v-model="maxDPI" name="dpi" @change="changeMaxPDI">
        <option 
        v-for="(item,index) in dpis" 
        :key="index"
        :value="index">
        {{ index+1 }}
      </option>
      </select>
    </div>
    <div style="width: 100%; height:100px;display: flex;align-items: center;">
      <el-slider 
        v-model="dpiValue" 
        show-input
        :min=dpiMini
        :max=dpiMax
        :step=dpiStep
        @change="UpdateDPIValue">
      </el-slider>
    </div>
    <div style="width: 100%; height:150px; display: flex; flex-direction: row;justify-content: center;">
      <!-- <el-radio 
        v-for="(item,index) in dpis"
        :key="index"
        v-model="currentDPIIndex" 
        :label="index" 
        style="color:white;"
        @input="ChangeDPIIndex"
        border>
        {{ item.value }}
        </el-radio> -->
        <div 
        v-for="(option,index) in dpis"
        :key="index">
        <dpi-radio 
        v-model="currentDPIIndex"  
        :maxDPI = "maxDPI"       
        :val="option.value"
        :color="option.color"
        :index="index" name="DPIRadioGroup"/>
        </div>

    </div>

    <!-- <div style="background-color: yellow;width: 100%;display: flex; flex-direction: row;">
        <el-color-picker 
        v-for="(item,index) in dpis" 
        :key="index"
        v-model="item.color" 
        size="mini"></el-color-picker>
    </div> -->
  </div>
</template>

<script>
  import HIDHandle from './HIDHandle';
  import DpiRadio from './DpiRadio';

  export default {
    components: {
      DpiRadio
    },
    data() {
      return {
        sensor:"",
        currentDPIIndex: 4,
        dpiValue:400,
        maxDPI:6,
        dpis: [
          {
            value:400,
            color:"#FF0000"
          },
          {
            value:800,
            color:"#00FF00"
          },   
          {
            value:1600,
            color:"#0000FF"
          },
          {
            value:3200,
            color:"#FFFF00"
          },  
          {
            value:6400,
            color:"#FF00FF"
          },
          {
            value:8000,
            color:"#00FFFF"
          },   
          {
            value:10000,
            color:"#FFFFFF"
          },
          {
            value:12000,
            color:"#FFFFFF"
          },        
        ],
        dpiArray:[],
        dpiValueArray:[],
        dpiRange:{},
        dpiMini:50,
        dpiMax:32000,
        dpiStep:50,
      }
    },
    methods: {
      ChangeDPIValue() {
        this.dpis[this.currentDPIIndex].value = this.dpiValue;
      },
      async UpdateDPIIndex() {
        await HIDHandle.setCurrentDPI(this.currentDPIIndex);
      },
      async changeMaxPDI() {
        console.log(this.maxDPI+ 1);
        await HIDHandle.setMaxDPI(this.maxDPI+ 1);
      },
      async UpdateColor(value) {
        let hex = value[1].slice(1).split('');
        let rgb = [
          parseInt(hex.splice(0, 2).join(''), 16), // 取两个字符并转换为10进制，然后添加到数组
          parseInt(hex.splice(0, 2).join(''), 16),
          parseInt(hex.splice(0, 2).join(''), 16)
        ];
        await HIDHandle.setDPIColor(rgb, Number.parseInt(value[0]));
      },
      async UpdateDPIValue() {
        this.dpis[this.currentDPIIndex].value = this.dpiValue;
        if(this.sensor === "3395" || this.sensor === "3311")
        {
          var value = 9;
          await HIDHandle.setDPIValue(value,this.currentDPIIndex);
        }       
      },
      UpdateUI_DPIValue(index,value,dpiex) {
        var rate = dpiex & 0x11 === 0x11 ? 2 : 1;
        var step = dpiex & 0x22 === 0x22 ? 2 : 1;
        var high = dpiex >> 6;
        
        if(this.sensor === "3395" || this.sensor === "3311")
        {
          var val = (high << 8) + value;
          this.dpis[index].value = (val + 1) * this.dpiStep * rate * step;
        }

      }
    },
    mounted() {
      this.$bus.$on('getDeviceInfo',value => {
        this.dpis = value.DPIs;

        this.sensor = value.Sensor;
        this.dpiRange = value.DPIRange;

        this.dpiMini = Number.parseInt(this.dpiRange.Mini[0]);
        this.dpiMax = Number.parseInt(this.dpiRange.Max[this.dpiRange.Max.length - 1]);
        this.dpiStep = Number.parseInt(this.dpiRange.Step[0]);
        if(this.sensor === "3395")
        {

        }
        else
        {

        }


        console.log('DPIs',this.dpis);
      });

      this.$bus.$on('DpiUpdateIndex',value => {
        this.currentDPIIndex = Number.parseInt(value);
        this.dpiValue =  Number.parseInt(this.dpis[this.currentDPIIndex].value);
        console.log('currentDPIIndex',this.currentDPIIndex);
        this.UpdateDPIIndex();
      });

      this.$bus.$on('DpiUpdateColor',value => {
        console.log('DpiUpdateColor',value);
        this.UpdateColor(value);
      });

      this.$bus.$on('UpdateFlashMap',value => {
        this.maxDPI = value[2] - 1;
        this.currentDPIIndex = value[4];

        for(var i = 0;i < 6;i++)
        {
          const rgb = `#${value[0x2c + i*4 + 0].toString(16).padStart(2,'0')}${value[0x2c + i*4 + 1].toString(16).padStart(2,'0')}${value[0x2c + i*4 + 2].toString(16).padStart(2,'0')}`.toUpperCase();
          this.dpis[i].color = rgb;
          this.UpdateUI_DPIValue(i,value[0x0C + i * 4],value[0x0E + i * 4]);
          console.log("color",i,this.dpis[i]);
        }      
      });
    },
    beforeDestroy() {
      this.$bus.$off('getDeviceInfo');
      this.$bus.$off('DpiUpdateIndex');
      this.$bus.$off('DpiUpdateColor');
      this.$bus.$off('UpdateFlashMap');
    }
  }
</script>

<style lang="less" scoped>
span {
  font-size: 16px;
  margin-left: 20px;
}

select {
  width: 50px;
  height: 20px;
  margin: auto;
  margin-left: 10px;
}

.el-slider {
  width: 80%;
  margin: auto;
}

</style>