<template>
  <div v-show="index <= maxDPI">
    <label 
      class="radio-button">
      <input
        :value="index"
        type="radio"
        v-model="model"
        :name="name"
        :checked="getChecked"
        @click="selectOption(index)"
      />
      <span class="radio-button-inner">{{ val }}</span>
	    <input type="color" class="radio-button-color" 
      v-model="colorModel" 
      @click="selectColorIndex(index)" 
      @input="ChangeColor()">
    </label>
  </div>
</template>

<script>

  export default {
      props: {       
      name: {
        type: String,
        required: true
      },
      index:{},
      val:{},
      color:{
        type: String,
        required: true
      },
      maxDPI:{}
    },
    data() {
      return {
        currentIndex:3,
        localColor:this.color,
      }
    },
    methods: {
      selectOption(value) {
        this.$bus.$emit('DpiUpdateIndex', value);
      },
      selectColorIndex(index) {
        this.currentIndex = Number.parseInt(index);
      },
      ChangeColor() {
        var temp = [];
        temp[0] = this.currentIndex;
        temp[1] = this.localColor;
        this.$bus.$emit('DpiUpdateColor', temp);
      }
    },
    computed: {
        model: {
        get() {
          return this.$parent.currentDPIIndex;
        },
        set(val) {
          console.log("set Model",val);
          this.$emit('input',val);
        }
      },
      colorModel: {
        get() {
          return this.$parent.dpis[this.index].color;
        },
        set(val) {
          this.localColor = val;
        }
      },
      getChecked() {
        var check = this.index === this.$parent.currentDPIIndex;
        return check;
      }
    }
  }
</script>

<style lang="less" scoped>
.radio-button {
    height: 100px;
    width: 100px;
    border: 2px solid #999;
    border-radius: 3%;
    margin-right: 50px;
    display: flex;
	  flex-direction: column; 
    position: relative;
  }
 
  .radio-button input[type="radio"] {
    opacity: 0;
    position: absolute;
  }
	
  .radio-button input[type="radio"]:checked + .radio-button-inner {
    background-color: #FF7F00;
  }
	
  .radio-button-inner {
    height: 100%;
    width: 100%;
    border-radius: 0%;
    background-color: #fff;
    color:black;
    transition: background-color 0.3s;
    display: flex;
    align-items:center;
    justify-content: center; /* 水平居中，如果需要的话 */  
  }
	
	.radio-button-color {
		height: 20%;
		width: 100%;
		border-radius: 2%;
		padding:0px;
	}
	
	input[type="color"]::-webkit-color-swatch-wrapper {
        padding: 0px; /* 水平和垂直内边距 */
    }
	input[type="color"]::-webkit-color-swatch {
        border: 1px solid #fff; /* 可以设置边框样式 */
        padding: 0px; /* 颜色方块内的内边距 */
    }
</style>