<template>
  <div>
    <div style="display: flex;flex-direction: row;justify-content: flex-end;align-items: baseline;margin: 10px;">
      <span lan="Version">驱动版本:</span>
      <span >{{version}}</span>
    </div>
    <div style="display: flex;flex-direction: column;align-items: flex-start;">
      <div style="margin: 20px;">
        <select v-model="selectLan" name="lan" @change="lanChange">
          <option v-for="item in lanOptions" :key="item.short" :value="item.short" >{{ item.text }}</option>
        </select>
      </div>
    </div>
      <common-device-info></common-device-info>
      <common-pair></common-pair>
      <common-long-distance></common-long-distance>
  </div>
</template>

<script>
  import CommonDeviceInfo from '../components/CommonDeviceInfo'
  import CommonPair from '../components/CommonPair'
  import CommonLongDistance from '../components/CommonLongDistance'

  export default {
    components: {
      CommonDeviceInfo,
      CommonPair,
      CommonLongDistance,
    },
    data() {
      return {
        version:"1.0.0.0",
        lanOptions:[],
        selectLan:"zh-cn"
      }
    },
    methods: {
      lanChange() {
        console.log(this.selectLan,this.lanOptions);
        let lanFile = {};

        for(let i = 0;i < this.lanOptions.length;i++)
        {
          if(this.lanOptions[i].short === this.selectLan)
          {
            lanFile = this.lanTypes[i];
            this.$gLanShort = this.selectLan;
            this.$gLanIndex = i;
            break;   
          }
        }
        console.log(lanFile);
        let elements = document.querySelectorAll('[lan]');

        // 遍历每个需要翻译的元素
        elements.forEach(element => {
        let id = element.getAttribute('lan');  
        if (lanFile[id]) {
            element.innerText = lanFile[id]; // 使用翻译后的文本替换原来的文本
        }
        });
      },
    },
    created() {
      this.lanTypes = this.$gLanTypes;
      this.lanOptions = this.$gLanOptions;
      this.selectLan = this.$gLanShort;
    },
  }
</script>

