<template>
  <div class="main">
    <span>高级设置</span>
    <div>
      <el-checkbox v-model="longDistance" @change="change">远距离模式</el-checkbox>
      <span>(远距离模式下，距离会更远)</span>
    </div>
  </div>
</template>

<script>
  import HIDHandle from './HIDHandle'

  export default {
    components: {
      HIDHandle
    },
    data() {
      return {
        longDistance:false,
      }
    },
    methods: {
      async change() {
        await HIDHandle.setLongDistance(this.longDistance ? 1 : 0);
      }
    },
    mounted() {
      this.$bus.$on('GetHIDParam',value => {
        this.longDistance = value.longDistance === 1;
      });
    }, 
    beforeDestroy() {
      this.$bus.$off('GetHIDParam');
    }
  }
</script>

<style lang="less" scoped>
.main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 20px;
}

/deep/ .el-checkbox__input.is-checked  {
  .el-checkbox__inner {
    background-color: #ff7f00;
    border-color: #ff7f00;
  }
}

/deep/ .el-checkbox .el-checkbox__label {
  color:#FFF;
}
</style>