<template>
  <el-carousel :interval="4000" type="card" height="380px">
    <el-carousel-item v-for="(image,index) in devicesPaths" :key="index">
      <img :src="image" @click="connectDevice(index)">
    </el-carousel-item>
  </el-carousel>
</template>

<script>
  import HIDHandle from './HIDHandle'
  export default {
    data() {
      return {
        devicesPaths:[
          './img/1Home/dev1online.png'
        ],
      }
    },
    methods: {
      // 选择需要操作的HID设备
      async connectDevice(index) {
        var connect = false;
        connect = await HIDHandle.requestDevice(index,this.$gConfig.Dev);
        if(connect)
        {
          this.$gDeviceCfg =this.$gConfig.Dev[index];
          console.log("gDeviceCfg",this.$gDeviceCfg);
          this.$bus.$emit("getDeviceInfo",this.$gConfig.Dev[index]);
          this.$bus.$emit('enterMain',true);
        }
      }
    },
    created() {
      for(var i = 0;i < this.$gConfig.Dev.length;i++)
        {
          this.devicesPaths[i] = './img/1Home/dev' + (i+1) + 'online.png';
        }
    },
  }
</script>

<style lang="less" scoped>
  .devices {
    margin-left: 20px;
    margin-top: 20px;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>