<template>
  <div style="display: flex;flex-direction: column;">
      <common-dpi></common-dpi>
      <common-report-rate></common-report-rate>
      <common-sensor v-show="showSensor"></common-sensor>
      <common-dpi-effect v-show="showDPIEffect"></common-dpi-effect>
  </div>
</template>

<script>
  import CommonDpi from '../components/CommonDpi'
  import CommonReportRate from '../components/CommonReportRate'
  import CommonSensor from '../components/CommonSensor'
  import CommonDpiEffect from '@/components/CommonDpiEffect'

  export default {
    components: {
      CommonDpi,
      CommonReportRate,
      CommonSensor,
      CommonDpiEffect
    },
    data () {
      return {
        showSensor:true,
        showDPIEffect:true,
      }
    },
    mounted() {
      this.$bus.$on('getDeviceInfo',value => {
        var sensor = value.Sensor;
        var showMode = this.$gSensorCfg.sensorMode.includes(sensor);
        var showLOD = this.$gSensorCfg.lod.includes(sensor);
        var showRipple = this.$gSensorCfg.ripple.includes(sensor);
        var showFixLine = this.$gSensorCfg.fixLine.includes(sensor);
        var showMotionSync = this.$gSensorCfg.motionSync.includes(sensor);

        this.showSensor = showMode || showLOD || showRipple || showFixLine || showMotionSync;
      });
    },
    beforeDestroy() {
      this.$bus.$off('getDeviceInfo');
    }   
  }
</script>

<style lang="less" scoped>
div {
  margin-bottom: 20px;
  padding: 0;
  overflow: hidden;
}
</style>