<template>
  <div>
    <span style="margin-left:20px">DPI灯效</span>
    <div class="main">
      <div class="effect">
        <select v-model="mode" @change="changeMode" name="effect">
          <option 
            v-for="(item,index) in effectModes"
            :key="index" 
            :value="item.value">
            {{ item.option }}
          </option>
        </select>
      </div>
      <div class="setting">
        <div class="speed">
          <span>速度</span>
          <el-slider 
            v-model="speed" 
            :min="1"
            :max="5"
            :step="1"
            @change="ChangeSpeed"
            :disabled="this.mode == 0 || this.mode == 1">
          </el-slider>
        </div>
        <div class="bri">
          <span>亮度</span>
          <el-slider 
            v-model="bri" 
            :min="1"
            :max="10"
            :step="1"
            @change="ChangeBri"
            :disabled="this.mode == 0 || this.mode == 2">
          </el-slider>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import HIDHandle from './HIDHandle';

  export default {
    components: {
      HIDHandle
    },
    data() {
      return {
        speed:3,
        bri:5,
        effectModes:[],
        mode:0
      }
    },
    methods: {
      async changeMode() {
        console.log("DPI Effect mode",this.mode); 
        if(this.mode == 0)
          await HIDHandle.setDPILightOff();
        else
          await HIDHandle.setDPILightEffect(this.mode);
      },
      async ChangeSpeed() {
        await HIDHandle.setDPILightSpeed(this.speed);
      },
      async ChangeBri() {
        await HIDHandle.setDPILightBri(HIDHandle.IndexToDPILightBri(this.bri));
      }
    },
    mounted() {
      this.effectModes = this.$gLanTypes[this.$gLanIndex].DPIEffectOption;
      this.$bus.$on('UpdateFlashMap',value => {  
        this.speed = value[0x50];
        this.bri = HIDHandle.DPILightBriToIndex(value[0x4E]);
        this.mode = value[0x52] === 0?0:value[0x4C];  
      });
    },
    beforeDestroy() {
      this.$bus.$off('UpdateFlashMap');
    }
  }
</script>

<style lang="less" scoped>
span {
  font-size: 16px;
  display: flex;
  margin: auto;
}

select {
  font-size: 12px; 
  width: 120px; 
  height: 25px; 
  text-align: center;
}

.main {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.effect {
  margin-left: 120px;
}

.setting {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 100px;
}

.speed, .bri {
  display: flex;
  flex-direction: row;
  width: 400px;
  height: 50px;
  margin: auto;
}

.el-slider {
  width: 300px;
  margin: auto;
}
</style>