<template>
  <div class="main">
    <div class="lightMode">
      <h1>灯光模式</h1>
      <select v-model="mode" name="lightMode" class="lightSelect" @change="modeChange">
        <option
          style="font-size: 16px"
          v-for="(item,index) in lightModes"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
      </select>
    </div>

    <div class="brightness" >
      <h1>亮度</h1>
      <el-slider
        :min="1" 
        :max="10"
        v-model="bri" 
        style="width: 250px; height: 20px;"
        @change="briChange"
        :disabled="disableBri">
      </el-slider>
    </div>

    <div class="speed">
      <h1>速度</h1>
      <el-slider 
        v-model="speed" 
        :min="1" 
        :max="10"
        style="width: 250px; height: 1px;"
        @change="speedChange"
        :disabled="disableSpeed">
      </el-slider>
    </div>

    <div class="lightOff">
      <span>移动式灯光关闭</span>
      <el-switch
        v-model="lightState"
        active-color="#13ce66"
        inactive-color="#ff4949"
        @change="stateChange">
      </el-switch>
    </div>

    <div class="lightOffTime">
      <span>放停后灯光关闭时间</span>
      <select v-model="time" name="lightOffTime" @change="timeChange">
        <option
        v-for="(item,index) in lightOffTimes"
        :key="index" 
        :value="item.value">
        {{ item.option }}
        </option>
      </select>
    </div>
  </div>
</template>

<script>
  import HIDHandle from "./HIDHandle"
  export default {
    components: {
      HIDHandle
    },
    data() {
      return {
        mode:1,
        lightModes:[],
        bri : 3,
        speed: 7,
        lightState: false,
        time:6,
        lightOffTimes:[],
        disableBri:false,
        disableSpeed:false,
      }
    },
    methods: {
      async modeChange() {
        if(this.mode == 0)
        {
          await HIDHandle.setLightOff();
        }
        else
        {
          await HIDHandle.setLightMode(Number.parseInt(this.mode));
        }
        this.disableBriAndSpeed();
      },
      async briChange() {
        await HIDHandle.setLightBri(Number.parseInt(this.bri - 1));
      },
      async speedChange() {
        await HIDHandle.setLightSpeed(Number.parseInt(this.speed - 1));
      },
      async stateChange() {
        await HIDHandle.setMoveLightOffState(this.lightState ? 1 : 0);
      },
      async timeChange() {
        await HIDHandle.setMoveLightOffTime(Number.parseInt(this.time));
      },
      disableBriAndSpeed() {
         /*
            0X01: 彩色流动（默认）（支持调速、调亮度，不支持调颜色）
            0X02: 单色呼吸（支持调速、调亮度、调颜色）
            0X03: 混色常亮（支持调亮度、颜色，不支持调速度）
            0X04: 霓虹（支持调速、调亮度，不支持调颜色）
            0X05: 混彩呼吸（支持调速、调亮度，不支持调颜色）
            0X06: 炫彩常亮（支持调速、调亮度，不支持调颜色）
            */
            switch (Number.parseInt(this.mode))
            {
                case 0:
                    this.$parent.showColor = false;
                    this.disableBri = true;
                    this.disableSpeed = true;
                    break;

                case 1:
                case 4:
                case 5:
                    this.$parent.showColor = false;
                    this.disableBri = false;
                    this.disableSpeed = false;
                    break;

                case 6:
                    this.$parent.showColor = false;
                    this.disableBri = false;
                    this.disableSpeed = true;
                    break;

                case 2:
                    this.$parent.showColor = true;
                    this.disableBri = false;
                    this.disableSpeed = false;
                    break;

                case 3:
                    this.$parent.showColor = true;
                    this.disableBri = false;
                    this.disableSpeed = true;
                    break;

                default:
                    break;
            }
      }
    },
    mounted() {
      this.lightModes = this.$gLanTypes[this.$gLanIndex].LightModeOption;
      this.lightOffTimes = this.$gLanTypes[this.$gLanIndex].LightOffTimeOption;
      this.$bus.$on('UpdateFlashMap',value => {  
        this.mode = value[0xA7] === 0 ? 0 : value[0xA0];
        this.speed = value[0xA4] + 1;
        this.bri = value[0xA5] + 1;  
        this.time = value[0xAD];
        this.lightState = value[0xB3] === 1;

        this.disableBriAndSpeed();
      });      
    },
    beforeDestroy() {
      this.$bus.$off('UpdateFlashMap');
    }
  }
</script>

<style lang="less" scoped>
h1 {
  font-size: 18px;
}

span {
  font-size: 14px;
  margin-right: 40px;
}

.main {
  display: flex;
  flex-direction: column;
}

.lightMode, .brightness, .speed {
  height:100px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
} 

.lightOff, .lightOffTime {
  height:80px;
  display: flex;
  flex-direction: row; 
  justify-content: flex-start;
}

select {
  font-size: 18px; 
  width: 80px;
  height: 25px; 
  text-align: center;
}

.lightSelect {
  width: 200px;
  height: 35px; 
}
</style>