<template>
  <div>
    <span style="margin-left:20px">Sensor设置</span>
    <div style="display: flex;flex-direction: row;">
      <div v-show="showMode" class="sensorMode">
        <span>模式选择</span>
        <select v-model="mode" name="sensorMode" @change="modeChange">
          <option 
          v-for="(item,index) in sensorModes"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
        </select>
      </div>

      <div v-show="showLOD" class="lod">
        <span>LOD高度</span>
        <select v-model="lod" name="sensorLOD" @change="lodChange">
          <option 
          v-for="(item,index) in lods"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
        </select>
      </div>

      <div v-show="showPerformance" class="performance">
        <el-checkbox v-model="performance" @change="performanceChange">火力全开</el-checkbox>
        <select 
        v-model="time"  
        @change="performanceTimeChange" 
        :disabled="performance === false"
        name="performance">
          <option 
          v-for="(item,index) in performances"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
        </select>
      </div>

      <div class="sensorSetting">
        <div v-show="showRipple" class="ripple">
          <el-switch
            v-model="rippleState"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="rippleChange">
          </el-switch>
          <span>波纹修正</span>
        </div>
        <div v-show="showFixLine" class="fixLine">
          <el-switch
            v-model="fixLineState"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="fixLineChange">
          </el-switch>
          <span>直线修正</span>
        </div>
        <div v-show="showMotionSync" class="motionSync">
          <el-switch
            v-model="motionSyncState"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="motionSyncChange">
          </el-switch>
          <span>Motion Sync</span>
        </div>
      </div>    
    </div>
  </div>
 
</template>

<script>
  import HIDHandle from './HIDHandle'
  export default {
    data() {
      return {
        showMode:true,
        showLOD:true,
        showPerformance:true,
        showRipple:true,
        showFixLine:true,
        showMotionSync:true,
        rippleState:false,
        fixLineState:false,
        motionSyncState:false,
        mode:0,
        sensorModes:[],
        lod:1,
        lods:[],
        time:3,
        performance:false,
        performances:[],
      }
    },
    methods: {
      async modeChange() {
        await HIDHandle.setSensorMode(this.mode);
      },
      async lodChange() {
        console.log("lod:",this.lod);
        await HIDHandle.setLOD(Number.parseInt(this.lod));
      },
      async performanceChange() {
        await HIDHandle.setPerformanceState(this.performance ? 1 : 0);
      },     
      async performanceTimeChange() {
        await HIDHandle.setPerformanceTime(this.time);
      },    
      async rippleChange() {
        await HIDHandle.setRipple(this.rippleState ? 1 : 0);
      },
      async fixLineChange() {
        await HIDHandle.setFixLine(this.fixLineState ? 1 : 0);
      },
      async motionSyncChange() {
        await HIDHandle.setMotionSync(this.motionSyncState ? 1 : 0);
      }
    },
    mounted() {
        this.sensorModes = this.$gLanTypes[this.$gLanIndex].SensorModeSelectOption;
        this.lods = this.$gLanTypes[this.$gLanIndex].LODOption;
        this.performances = this.$gLanTypes[this.$gLanIndex].PerformanceOption;

        this.$bus.$on('getDeviceInfo',value => {
          var sensor = value.Sensor;
          this.showMode = this.$gSensorCfg.sensorMode.includes(sensor);
          this.showLOD = this.$gSensorCfg.lod.includes(sensor);
          this.showRipple = this.$gSensorCfg.ripple.includes(sensor);
          this.showFixLine = this.$gSensorCfg.fixLine.includes(sensor);
          this.showMotionSync = this.$gSensorCfg.motionSync.includes(sensor);
          console.log("sensor:",sensor,this.$gSensorCfg);
        });
        
        this.$bus.$on('UpdateFlashMap',value => {  
          this.mode = value[0xB9];
          this.lod = value[0x0A];
          this.performance = value[0xB5] === 1;  
          this.time = value[0xB7];
          this.motionSyncState = value[0xAB] === 1;
          this.fixLineState = value[0xAF] === 1;
          this.rippleState = value[0xB1] === 1;
        });
    },
    beforeDestroy() {
      this.$bus.$off('getDeviceInfo');
      this.$bus.$off('UpdateFlashMap');
    }
  }
</script>

<style lang="less" scoped>
span {
  font-size: 16px;
  display: flex;
  align-items: flex-start;
}

.sensorMode, .lod, .performance {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

select {
  margin-top:40px;
  position: absolute; 
  font-size: 12px; 
  width: 120px; 
  height: 25px; 
  text-align: center;
}

.sensorSetting {
  margin-top: 30px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.ripple, .fixLine, .motionSync {
  padding: 5px;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/deep/ .el-checkbox__input.is-checked  {
  .el-checkbox__inner {
    background-color: #ff7f00;
    border-color: #ff7f00;
  }
}

/deep/ .el-checkbox .el-checkbox__label {
    color:#FFF;
  }
</style>