async function loadArrayBuffer(address,value) {
  let data = Uint8Array.of(0x07, 0x00, address >> 8, address & 0xFF, 0x00, 0x00, 0x00,
  0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0xef);
  let crc = 0;
  data[4] = value.length + 1;
  for(let i = 0;i < value.length;i++)
  {
    data[5 + i] = value[i];
    crc += value[i];
  }
  console.log("crc:",crc);
  data[5 + value.length] = 0x55 - crc;
  crc = getUsbCrc(data);
  data[15] = crc;

  await write(data);
}

async function loadByteBuffer(address,value) {
  let data = Uint8Array.of(0x07, 0x00, address >> 8, address & 0xFF, 0x02, 0x08, 0x4d,
  0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0xef); // 示例数据 

  data[5] = value;
  data[6] = 0x55 - value;
  let crc = getUsbCrc(data);
  data[15] = crc;

  await write(data);
}

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

async function loadArrayCommand(com,value) {
  let data = Uint8Array.of(com, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
  0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0xef);
  let crc = 0;
  data[4] = value.length;
  for(let i = 0;i < value.length;i++)
  {
    data[5 + i] = value[i];
  }

  crc = getUsbCrc(data);
  data[15] = crc;

  await write(data);
}

async function loadByteCommand(com) {
  let data = Uint8Array.of(com, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
  0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0xef); // 示例数据 

  let crc = getUsbCrc(data);
  data[15] = crc;

  await write(data);
}

async function getOnline() {
  await loadByteCommand(0x03);

  if(receivedData[5] === 1)
    return true;
  else
    return false;
} 

