async function setMultimedia(multimedia,index)
{
  var addr = 0x100 + index * 0x20;
  var value = [];

  var cnt = 0;
  value[cnt++] = 0x02;
  value[cnt++] = 0x82;
  value[cnt++] = multimedia & 0xFF;
  value[cnt++] = multimedia >> 8;
 
  value[cnt++] = 0x42;
  value[cnt++] = multimedia & 0xFF;
  value[cnt++] = multimedia >> 8;

  await loadArrayBuffer(addr,value);  
}

async function setShortCutKey(shortCut,index) {
  var addr = 0x100 + index * 0x20;
  var value = [];
  var str = shortCut.toLowerCase();
  var cnt = 0;
  if(str.indexOf("ctrl") !== -1)
  {
    value[cnt++] = 0x80;
    value[cnt++] = 0x01;
    value[cnt++] = 0x00;
  }



  await loadArrayBuffer(addr,value);
}

function setSensor(value)
{
  sensor = value;
}

