import Vue from 'vue'
import App from './App.vue'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
import router from './router'
import './assets/icon-macro/iconfont.css'
import HIDHandle from './components/HIDHandle'

import config from './cfg.json'
import LanEn from './lan/en.json'
import LanZhCN from './lan/zh-cn.json'

Vue.config.productionTip = false;

//按需引入
Vue.use(ElementUI)
Vue.use(HIDHandle)

Vue.prototype.$gConfig = config;

new Vue({
  router,
  el:'#app',
  render: h => h(App),
  beforeCreate() {
    Vue.prototype.$bus = this //安装全局在时间总线
    Vue.prototype.$gDeviceCfg;

    Vue.prototype.$gLanIndex = 1;
    Vue.prototype.$gLanShort = "zh-cn";

    Vue.prototype.$gSensorDPI;

    // 获取所有语言
    var languages = [];
    languages.push(LanEn);
    languages.push(LanZhCN);
    Vue.prototype.$gLanTypes = languages;

    // 获取语言下拉框选项
    const fileNames = [];
    const req = require.context('./lan', false, /\.(json)$/);
    this.$gLanTypes = req.keys().map(path => req(path));
    req.keys().map(path => {
      const fileNmae = path.split('/').pop().replace(/\.[^/.]+$/, '');

      fileNames.push(fileNmae);
    })

    var lanOptions = [];
    for(let i = 0;i < fileNames.length;i++)
    {
      let lanSelect = {
        short:fileNames[i],
        text:this.$gLanTypes[i].Language,
      }
      lanOptions.push(lanSelect);
    }

    Vue.prototype.$gLanOptions = lanOptions;

    var sensorCfg = {
      "sensorMode":"",
      "lod":"",
      "ripple":"",
      "fixLine":"",
      "motionSync":""
    };
    
    fetch('driver_sensor.h')
    .then(response => response.text())
    .then(text => {
      const lines = text.split('\n');

      Vue.prototype.$gSensorDPI = lines;
      for(var i = 0;i < 10;i++)
      {
        if(lines[i].includes("SUPPORT_SENSOR_MODE_SEL"))
        {
          const sp = lines[i].split('\t');
          sensorCfg.sensorMode = sp[sp.length - 1].replace('\r','');
        }

        if(lines[i].includes("SUPPORT_LOD_CAL"))
        {
          const sp = lines[i].split('\t');
          sensorCfg.lod = sp[sp.length - 1].replace('\r','');
        }

        if(lines[i].includes("SUPPORT_RIPPLE"))
        {
          const sp = lines[i].split('\t');
          sensorCfg.ripple = sp[sp.length - 1].replace('\r','');
        }

        if(lines[i].includes("SUPPORT_FIXLINE"))
        {
          const sp = lines[i].split('\t');
          sensorCfg.fixLine = sp[sp.length - 1].replace('\r','');
        }

        if(lines[i].includes("SUPPORT_MOTION_SYNC"))
        {
          const sp = lines[i].split('\t');
          sensorCfg.motionSync = sp[sp.length - 1].replace('\r','');
        }
      }
      Vue.prototype.$gSensorCfg = sensorCfg;
    })
    .catch(error => {
      console.error('Error reading file:', error);
    });
    console.log("main",config,languages,lanOptions);
  }
})
