<template>
  <div v-loading="loading">
      
    <div class="header">
      <common-tabs></common-tabs>
      <!-- <common-battery :capacity="100"></common-battery> -->
    </div>
    <div class="main">
      <common-aside></common-aside>
    </div>
  </div>
</template>

<script>
  import CommonTabs from '../components/CommonTabs'
  import CommonHeader from '../components/CommonHeader'
  import CommonAside from '../components/CommonAside'
  import HIDHandle from '../components/HIDHandle'
  import CommonBattery from '../components/CommonBattery'

  export default {
    components: {
      CommonHeader,
      CommonAside,
      CommonTabs,
      CommonBattery,
      HIDHandle
    },
    data() {
      return {
        loading:false,
        timerId:null,
        connectState:0,
        flashMap:[],
        updatUIFlag:false
      }
    },
    methods: {
      updateConnectState() {
        var state = HIDHandle.getConnectState();
        var param = HIDHandle.getHIDParameter();
        if(state === 1)
        {
          this.loading = true;
        }
        else {
          this.loading = false;

          if(state === 2)
          {
            if(this.updatUIFlag === false)
            {
              this.updatUIFlag = true;
              console.log("param:",param);
              this.$bus.$emit("UpdateFlashMap",HIDHandle.getFlashMap());
              this.$bus.$emit("GetHIDParam",param);
            }
          }
        }

      }
    },
    mounted() {
      this.timerId = setInterval(this.updateConnectState, 200);
      this.$bus.$on("UpdateUI",value => {
        this.updatUIFlag = value;
      })
    },
    beforeDestroy() {
      if(this.timerId)
      {
        clearInterval(this.timerId);
      }
    }
  }
</script>

<style lang="less" scoped>
div {
  display: flex;
  flex-direction: column;
}

.header {
  height: 120px;
  background-color: #131313;
  padding: 0;
}

.mian {
  background-color: #131313;
  padding: 0;
}
</style>