<template>
  <div class="main">
    <div class="lightColor">
      <h1>灯光颜色</h1>
      <el-image 
        src='./img/8General/Color/colorbar.png' 
        alt="Color Picker"
        @click="getPixelColor($event)" 
        ref="imageElement">
      </el-image>
    </div>
    
    <div class="colorPreview">
      <h1>颜色预览</h1>
      <div class="colorBox">
        <div class="box" :style="{backgroundColor:boxColor}"></div>
        <div class="colorNums">
          <div class="colorNum">
            <span>R:</span>
            <el-input-number v-model="colorR" @change="boxColorChange" :min="0" :max="255" size="mini"></el-input-number>
          </div>
          <div class="colorNum">
            <span>G:</span>
            <el-input-number v-model="colorG" @change="boxColorChange" :min="0" :max="255" size="mini"></el-input-number>
          </div>
          <div class="colorNum">
            <span>B:</span>
            <el-input-number v-model="colorB" @change="boxColorChange" :min="0" :max="255" size="mini"></el-input-number>
          </div>
        </div>
      </div>
    </div>
    <div class="colorPreset">
      <h1>预设颜色</h1>
      <div class="colorBlocks">
        <el-image 
          class="colorBlock"
          v-for="(item,index) in 7"
          :key="index"
          :src='`./img/8General/Color/color${item}.png`' 
          alt="Color Picker"
          @click="getPixelColor($event)" 
          ref="imageElement">
        </el-image>
      </div>
      <div class="colorBlocks">
        <el-image 
          class="colorBlock"
          v-for="(item,index) in 7"
          :key="index"
          :src='`./img/8General/Color/color${item+7}.png`' 
          alt="Color Picker"
          @click="getPixelColor($event)" 
          ref="imageElement">
        </el-image>
      </div>
    </div>
  </div>



</template>

<script>
  import HIDHandle from './HIDHandle'
  export default {
    components:{
      HIDHandle
    },
    data() {
      return {
        boxColor:'#000',
        colorR:0,
        colorG:0,
        colorB:0,
      };
    },
    methods: {
      getPixelColor(event) {
        const imgElement = event.target;
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
  
        canvas.width = imgElement.naturalWidth;
        canvas.height = imgElement.naturalHeight;
        ctx.drawImage(imgElement, 0, 0);
  
        const pixelData = ctx.getImageData(
          event.offsetX,
          event.offsetY,
          1,
          1
        ).data;
        
        const rgb = `rgb(${pixelData[0]}, ${pixelData[1]}, ${pixelData[2]})`;
        this.boxColor = rgb;
        this.colorR = pixelData[0];
        this.colorG = pixelData[1];
        this.colorB = pixelData[2];
        this.updateColor();
      },
      boxColorChange() {
        const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
        this.boxColor = rgb;
        this.updateColor();
      },
      async updateColor() {
        var value = [];
        value[0] = this.colorR;
        value[1] = this.colorG;
        value[2] = this.colorB;  
        await HIDHandle.setLightColor(value);       
      }
    },
    mounted() {
      this.$bus.$on('UpdateFlashMap',value => {  
        this.colorR = value[0xA1];
        this.colorG = value[0xA2];
        this.colorB = value[0xA3];

        const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
        this.boxColor = rgb;
      });      
    },
    beforeDestroy() {
      this.$bus.$off('UpdateFlashMap');
    }
  };
</script>

<style lang="less" scoped>
.main {
  display: flex;
  flex-direction: column;
}

.lightColor, .colorPreview, .colorPreset, .colorBox, .colorNums{
  height:100px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.colorPreview {
  height:200px;
}

.colorBox {
  margin-top: 20px; 
  flex-direction: row;
}

.box {
  border: 0px solid #000; /* 边框样式 */
  width: 100px;           /* 宽度 */
  height: 100px;          /* 高度 */
}

.colorNums {
  align-items: flex-end;
  margin-left: 20px;
}

.colorNum {
  height:30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.el-input-number {
  margin-left: 10px;
}

.colorBlocks {
  height:40px;
  display: flex;
  flex-direction: row; 
  justify-content: center;
  flex-wrap: wrap;
}

.colorBlock {
  height: 30px;
  width: 30px;
  margin-right: 20px;
  margin-top: 20px;
}

</style>