<template>
    <div style="display: flex; flex-direction: column;">
      <div style="display: flex;flex-direction: row;justify-content: flex-start;margin: 20px;">
        <select v-model="profile" name="profile">
          <option           
            style="font-size: 16px"
            v-for="(item,index) in profiles"
            :key="index" 
            :value="item.value">
            {{ item.option }}
          </option>
        </select>
      </div>
      <div style="display: flex; flex-direction: row;justify-content: space-around">
        <div
        style="display: flex;flex-direction: column;margin: 50px;">
          <div 
          v-for="(item,index) in Locs" 
          :key="index">
            <el-cascader 
              v-model="keys[index]"
              :options="keyOptions" 
              :show-all-levels="false" 
              placeholder=""
              @change="selectChange(index)"
              style="margin-top: 20px;">
            </el-cascader>
          </div>
        </div>

        <div
        style="display: flex;flex-direction: column;justify-content: center;align-items: flex-end;">
        <img :src="img">
        </div>
      </div>
      <div
      style="display: flex;flex-direction: row;justify-content: flex-end;margin-right: 20px;">
      <el-button lan="Restore" @click="restore">
        恢复默认
      </el-button>
      <label class="import-button">
        <input type="file" @click="importFile" class="importFile">
        <span class="import-button-inner" lan="ImportFile">导入配置</span>
      </label>
      <el-button lan="ExportFile" @click="exportFile">
        导出配置
      </el-button>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span>这是一段信息</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>       
    </div>
</template>

<script>
  import HIDHandle from '../components/HIDHandle'

  export default {
    data() {
      return {
        loadFinish:false,
        dialogVisible: false,
        keyOptions:[],
        profile:0,
        profiles:[],
        keys:[],
        Locs:[],
        img: "./img/2Main/dev1.png",

      }
    },
    methods: {
      restore() {

      },
      importFile(event) {
        const file = event.target.files[0];
        const reader = new FileReader();
        reader.onload = e => {
          // 处理读取结果，e.target.result包含文件内容
          console.log(e.target.result);
        };
        reader.readAsText(file); // 根据文件类型可能需要使用其他读取方法，如readAsArrayBuffer
      },
      exportFile() {
        const data = "文件内容";
        const blob = new Blob([data], { type: 'text/plain;charset=utf-8' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "文件名.txt");
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      },
      async selectChange(index) {       
        let data = [];
        var value = this.keys[index];
        var shortcutKey = false;

        data[0] = Number.parseInt(value[0]);
        if(data[0] === 0x05)
        {
          data[1] = 0;
          data[2] = 0;

          shortcutKey = true;
        }
        else
        {
          let tmp = Number.parseInt(value[1]);
          data[1] = tmp >> 8;
          data[2] = tmp & 0xFF;
        }

        console.log(value,index,data);
        await HIDHandle.setKeyFunction(data,index);

        if(shortcutKey)
        {
          await HIDHandle.setMultimedia(value[1],index);
        }
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    },
    mounted() {
      this.keyOptions = this.$gLanTypes[this.$gLanIndex].KeysFun;
      this.profiles = this.$gLanTypes[this.$gLanIndex].ProfileOption;

      this.$bus.$on('getDeviceInfo',value => {
        for(var i = 0;i < value.Keys.length;i++)
        {
          this.keys[i] = value.Keys[i].value;
        }

        console.log('keys',this.keyOptions,this.keys);
        this.Locs = [];
        for(var i = 0;i < this.keys.length;i++)
        {
          var loc = 
          {
            position: 'absolute',
            top: value.Keys[i].Loc[0] + 'px',
            left: value.Keys[i].Loc[1] + 'px'
          }
          this.Locs.push(loc);
        }
        
        this.loadFinish = true;
      });
      
      this.$bus.$on('GetHIDParam',value => {  
        this.profile = value.profileIndex;
      }); 

      this.$bus.$on('UpdateFlashMap',value => { 
        this.loadFinish = false; 
        for(var i = 0;i < this.keys.length;i++)
        {
          var tmp = value[0x60 + i * 4 + 1] << 8 + value[0x60 + i * 4 + 2];
          this.keys[i] = [value[0x60 + i * 4 + 0].toString(16),'0x'+tmp.toString(16).padStart(4,'0')];       
        }

        console.log("keys:",this.keyOptions,this.keys);
        this.loadFinish = true;
      }); 
    },
    beforeDestroy() {
      this.$bus.$off('getDeviceInfo');
      this.$bus.$off('GetHIDParam');
      this.$bus.$off('UpdateFlashMap');
    }
  }
</script>

<style lang="less" scoped>
.select {
  width: 200px;
}

.el-button {
  background-color: transparent;
  color:white;
}

.import-button {
  width: 100px;
  height: 40px;
  border: 1px solid #FFF;
  border-radius: 3%;
}

.import-button input[type="file"] {
    opacity: 0;
    position: absolute;
}

.import-button-inner {
  font-weight: 500;
  height: 100%;
  width: 100%;
  border-radius: 0%;
  background-color: transparent;
  color:white;
  transition: background-color 0.3s;
  display: flex;
  align-items:center;
  justify-content: center; /* 水平居中，如果需要的话 */  
}  
</style>