<template>
  <div>
    <el-container>
        <el-header>
          <select class="lanSelect" v-model="selectLan" @change="lanChange">
            <option v-for="item in lanOptions" :key="item.short" :value="item.short" >{{ item.text }}</option>
          </select>
        </el-header>
        <el-main>
          <welcome-main />
        </el-main>
        <el-footer>
          <div class="footer">Footer</div>
        </el-footer>
    </el-container>
  </div>
</template>

<script>
  import WelcomeMain from '../components/WelcomeMain';

  export default {
    components: 
    {
       WelcomeMain 
    },
    data() {
      return {
        lanOptions:[],
        selectLan:"zh-cn",
        lanTypes:null,
      }
    },
    methods: {
      lanChange() {
        console.log(this.selectLan,this.lanOptions);
        let lanFile = {};

        for(let i = 0;i < this.lanOptions.length;i++)
        {
          if(this.lanOptions[i].short === this.selectLan)
          {
            lanFile = this.lanTypes[i];
            this.$gLanShort = this.selectLan;
            this.$gLanIndex = i;
            break;   
          }
        }
        console.log(lanFile);
        let elements = document.querySelectorAll('[lan]');

        // 遍历每个需要翻译的元素
        elements.forEach(element => {
        let id = element.getAttribute('lan');  
        if (lanFile[id]) {
            element.innerText = lanFile[id]; // 使用翻译后的文本替换原来的文本
        }
        });
      },
    },
    created() {
      this.lanTypes = this.$gLanTypes;
      this.lanOptions = this.$gLanOptions;
      this.selectLan = this.$gLanShort;
    },
  }
</script>

<style lang="less" scoped>
.el-header {
  background-color: #131313;
  padding: 0 0;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.el-main {
  background-color: #353535;
  color: #fff;
  height: 75vh;
}
.el-footer {
  background-color: #353535;
  color: #fff;
  text-align: center;
  .footer {
    margin: 10px;
    position: relative;
    top:20px;
  }
}
select {
  right:20px;
  margin:0 20px;
  position: absolute;
  background-color: #131313;
  color: #fff;
}
</style>